import React, { Component } from 'react';
import { Link, withRouter } from "react-router-dom"
import { Badge, Icon, List, Avatar, Button } from 'antd';
import './header.css'
import logo from './logo.png'
import ModalAuth from '../../containers/modal-auth';



class Header extends Component { 
  
  render() {

    const {badge, total, onRemovedToCart, onAddedToCart, onAllRemovedToCart, history, isAuth, user, signOut, showModal} = this.props;

    const authorized = <div> <Link to='/profile' >{user}</Link><Button onClick={signOut}>Выйти</Button></div>
    return (
      <header className='header'>
      <Link to="/"><img src={logo} alt='logo' width='140'/></Link>
      {isAuth ? authorized : 
      <React.Fragment> 
        <Button onClick={showModal}>
          Войти
        </Button> 
        <ModalAuth/>
      </React.Fragment>
      } 
      <Badge count={badge.reduce((acc, value) => value.count + acc, 0)}>
        <Icon type="shopping-cart" style={{ fontSize: 45, color: '#654321' }} />
      </Badge>

      <div className='modal-cart'>
        <List
          itemLayout="horizontal"
          dataSource={badge}
          renderItem={item => (
            <List.Item>
              <List.Item.Meta
                avatar={<Avatar src={item.images[0]} />}
                title={<Link to='#' className='modal-cart__item' onClick={(_id) => history.push(`/games/${item._id}`)}>{item.title}</Link>}
                />
              <div>
                <span>{item.price} ₽</span>
                <button  className='modal-cart__btn' onClick={() => onRemovedToCart(item._id)}><i className="fas fa-minus" /></button>
                <span className='modal-cart__count'>{item.count} шт.</span>
                <button className='modal-cart__btn' onClick={() => onAddedToCart(item._id)}><i className="fas fa-plus" /></button>
                <button className='modal-cart__btn' onClick={() => onAllRemovedToCart(item._id)}><i className="fas fa-trash-alt" /></button>
              </div>
            </List.Item>
          )}
          />
          <div className='modal-cart__badge'>
            <Link className='modal-cart__badge-link' to="/badge">Перейти в корзину</Link>
            <span className='modal-cart__badge-total'>Итого: {total} ₽</span>
          </div>
        </div>
      </header>
    )
  }
}

export default withRouter(Header)