import React from "react"
import { Form, Input, Button, Spin } from "antd"

class RegistrationForm extends React.Component {
  state = {
    confirmDirty: false,
    email: '',
    name: '',
    password: ''
  }
  

  handleSubmit = e => {
    e.preventDefault();    
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.signUp(this.state.email, this.state.password, this.state.name)
          .then(() => {
            if (this.props.isAuth) {
              return this.props.hideModal()
            }
          })
      }
    })
  }

  handleConfirmBlur = e => {
    const { value } = e.target
    this.setState({ confirmDirty: this.state.confirmDirty || !!value })
  }

  compareToFirstPassword = (rule, value, callback) => {
    const { form } = this.props
    if (value && value !== form.getFieldValue("password")) {
      callback("Two passwords that you enter is inconsistent!")
    } else {
      callback()
    }
  }

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props
    if (value && this.state.confirmDirty) {
      form.validateFields(["confirm"], { force: true })
    }
    callback()
  }


  handleInputChange = (event) => {
    const target = event.target;
    const value = target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  render() {
    
    const { getFieldDecorator } = this.props.form

    return (
      <Form onSubmit={this.handleSubmit} className="register-form">
        {this.props.error ? <p className='error-message'>Проверьте правильность ваших данных</p> : null}
        <Spin spinning={this.props.loading} >
          <Form.Item label="E-mail">
            {getFieldDecorator("email", {
              rules: [
                {
                  type: "email",
                  message: "The input is not valid E-mail!"
                },
                {
                  required: true,
                  message: "Please input your E-mail!"
                }
              ]
            })(<Input name='email' onChange={this.handleInputChange} />)}
          </Form.Item>
          <Form.Item label="Password" hasFeedback>
            {getFieldDecorator("password", {
              rules: [
                {
                  required: true,
                  message: "Please input your password!"
                },
                {
                  validator: this.validateToNextPassword
                }
              ]
            })(<Input.Password name='password' onChange={this.handleInputChange} />)}
          </Form.Item>
          <Form.Item label="Confirm Password" hasFeedback>
            {getFieldDecorator("confirm", {
              rules: [
                {
                  required: true,
                  message: "Please confirm your password!"
                },
                {
                  validator: this.compareToFirstPassword
                }
              ]
            })(<Input.Password onBlur={this.handleConfirmBlur}  />)}
          </Form.Item>
          <Form.Item label="Name">
            {getFieldDecorator('username', {
              rules: [
                {
                  required: true,
                  message: 'Please input your name',
                },
              ],
            })(<Input placeholder="Please input your name" name='name' onChange={this.handleInputChange} />)}
          </Form.Item>
        </Spin>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            Зарегистрироваться
          </Button>
        </Form.Item>
      </Form>
    )
  }
}

const WrappedRegistrationForm = Form.create({ name: "register" })(RegistrationForm)

export default WrappedRegistrationForm
