
import React from 'react';
import { Slider, InputNumber} from 'antd';

const Range = (props) => {

  const { onChangeRange, onAfterChangeRange, onChangeMin, onChangeMax,
     minPrice, maxPrice, value } = props;
  const [minCurrent, maxCurrent] = value;
  return (
    <div className='range'>
        <Slider
          range
          min={minPrice}
          max={maxPrice}
          onChange={onChangeRange}
          onAfterChange={onAfterChangeRange}
          defaultValue={[minPrice, maxPrice]}
          value={value}
        />
        <div className='range__input'>
          <InputNumber
            min={minPrice}
            max={maxPrice}
            value={minCurrent}
            onChange={onChangeMin}
            style={{marginLeft: '10px'}}
          />
          <InputNumber
            min={minPrice}
            max={maxPrice}
            value={maxCurrent}
            onChange={onChangeMax}
            style={{marginRight: '10px'}}
          />
        </div>
    </div>
  );
}


export default Range;