import React from 'react'
import { Button, Modal } from 'antd'
import SignIn from '../sign-in'
import Registration from '../registration-form'


const ModalAuth = (props) => {
  const {signIn, loading, error, signUp, visibleModal, signInOrUp, hideModal, changeSign, isAuth } = props;
  const title = signInOrUp ? <div><h3>Вход</h3><Button onClick={changeSign}>Зарегистрироваться</Button></div> : 
                                    <div><h3>Регистрация</h3><Button onClick={changeSign}>Войти</Button></div>;
  return (
    <React.Fragment> 
      <Modal
        visible={visibleModal}
        title={title}
        centered
        footer={null}
        onCancel={hideModal}
      >
        {
          signInOrUp ? 
          <SignIn  signIn={signIn} loading={loading} error={error} hideModal={hideModal} isAuth={isAuth}/> :
          <Registration signUp={signUp} loading={loading} error={error} hideModal={hideModal} isAuth={isAuth}/>
        }
      </Modal>
    </React.Fragment>
  )
}

export default ModalAuth;